const Position = {
    PG: 'PG',
    SG: 'SG',
    SF: 'SF',
    PF: 'PF',
    C: 'C',
};

class Team {
    constructor() {
        this.starterPg = null;
        this.starterSg = null;
        this.starterSf = null;
        this.starterPf = null;
        this.starterC = null;
        this.benchs = [];
        this.dnps = [];
    }

    get allPlayers() {
        const result = [this.starterPg, this.starterSg, this.starterC, this.starterSf, this.starterPf].filter(p => p);
        for (let p of this.benchs + this.dnps) {
            result.push(p);
        }
        return result;
    }
}

class PositionInfo {
    constructor() {
        this.position = Position.PG;
        this.secondPosition = Position.PG;
        this.id = '';
        this.minutes = 0;
    }
}

export {Team, Position}