import Util from '../util/util'
import {game} from './game'
import {Position} from "./team";
import Config from './basket.config'

const idGenerator = (function* nextId() {
    let i = 0;
    while (true) {
        yield 'Player#' + i++;
    }
})();

class Player {
    constructor() {
        this.ability = new Ability();
        this.stats = new Statistics();
    }

    get potentialRate() {
        return +((this.ability.potential * 5 / (Config.MAX_POTENTIAL - Config.MIN_POTENTIAL)).toFixed(1));
    }

    get abilityRate() {
        return +((this.ability.scoring * 5 / (Config.MAX_ABILITY - Config.MIN_ABILITY)).toFixed(1));
    }

    get info() {
        return `
        姓名  ${this.name}<br />
        年龄  ${this.age}<br />
        能力  ${this.ability.scoring}<br />
        位置  ${this.position}<br />
        辅助  ${this.secondPosition}
        `
    }

    static randomAndReg() {
        const player = this.random();
        game.registerPlayer(player);
        return player;
    }

    static random() {
        const player = new Player();
        player.age = Util.randAge(18, 22);
        player.head = Util.randImg();
        player.name = Util.randName();
        player.ability.scoring = Util.randNum(Config.MIN_ABILITY, Config.MAX_INIT_ABILITY);
        player.ability.potential = Util.randNum(Config.MIN_POTENTIAL, Config.MAX_POTENTIAL);
        player.id = idGenerator.next().value;
        player.position = Util.randPosition();
        player.secondPosition = Util.randSecondPosition(player.position);
        return player;
    }

    randomBattleWith(another) {
        const sThis = this.ability.scoring;
        const sThat = another.ability.scoring;
        const gap = sThis - sThat;
        let probability = 0;
        if (gap > 10) {
            probability = 0.8;
        } else if (gap > 5) {
            probability = 0.7;
        } else if (gap > 0) {
            probability = 0.6;
        } else if (gap === 0) {
            probability = 0.5;
        } else if (gap >= -5) {
            probability = 0.4;
        } else if (gap >= -10) {
            probability = 0.3;
        } else {
            probability = 0.2;
        }
        const result = new RandomBattleResult();
        if (probability > Math.random()) {
            result.winner = this;
            result.loser = another;
            result.userWin = true;
        } else {
            result.winner = another;
            result.loser = this;
            result.userWin = false;
        }
        return result;
    }
}

class RandomBattleResult {
    constructor() {
        this.winner = null;
        this.loser = null;
        this.userWin = false;
    }

    get textInfo() {
        if (this.userWin) {
            return `恭喜~您的球员${this.winner.name}获得了比赛胜利~`
        } else {
            return `ORZ…您的球员输了比赛`
        }
    }
}

class GameResult {
    constructor() {
        this.score = 0;
    }
}

class Ability {
    constructor() {
        this.scoring = 0;
        this.potential = 0;
        this.exp = 0;
    }
}

class Statistics {
    constructor() {
        this.totalScore = 0;
        this.totalGame = 0;
    }
}

export {Player}