import {Position} from "../core/team";

export default {
    randAge: function (min, max) {
        return Math.floor(Math.random() * (max - min) + min);
    },

    randName: function () {
        return ''
    },

    randImg: function () {
        const num = Math.floor(Math.random() * 30 + 1);
        return `player${num}`;
    },

    randInfo: function () {
        return '是个傻子';
    },

    randNum: function (min, max) {
        return Math.floor(Math.random() * (max - min) + min);
    },

    randPosition: function () {
        return [Position.C, Position.PF, Position.PG, Position.SF, Position.SG][Math.floor(Math.random() * 5)];
    },

    randSecondPosition: function (current) {
        switch (current) {
            case Position.PG:
                return [Position.PG, Position.SG][Math.floor(Math.random() * 2)];
            case Position.SG:
                return [Position.PG, Position.SF, Position.SG][Math.floor(Math.random() * 3)];
            case Position.SF:
                return [Position.PF, Position.SF, Position.SG][Math.floor(Math.random() * 3)];
            case Position.PF:
                return [Position.PF, Position.SF, Position.C][Math.floor(Math.random() * 3)];
            case Position.C:
                return [Position.PG, Position.C][Math.floor(Math.random() * 2)];
        }
    }
}