<template>
    <div>
        <el-row>
            <el-select v-model="selected" placeholder="请选择" value="">
                <el-option
                        v-for="p in players"
                        :key="p.id"
                        :label="p.name"
                        :value="p.id">
                </el-option>
            </el-select>
            <el-button round @click="startBattle()">开始</el-button>
        </el-row>
        <el-row>
            <el-card :style="styles.outerCard" :body-style="styles.infoCard">
                {{ resultText }}
            </el-card>
        </el-row>
    </div>
</template>

<script>
    import {game} from '../core/game'
    import {Player} from "../core/player";

    export default {
        name: "BasketGame",
        data() {
            return {
                selected: null,
                players: [],
                resultText: '请点击开始比赛~',
                styles: {
                    outerCard: {
                      marginTop: '20px'
                    },
                    infoCard: {
                        height: '300px',
                    }
                }
            }
        },
        mounted() {
            this.players = game.playerList;
            if (this.players.length <= 0) {
                this.$alert('您还没有球员~快去签一个吧~', '没有球员', {
                    confirmButtonText: '确定',
                    callback: action => {
                        this.$router.push('/team');
                    }
                });
            }
        },
        methods: {
            startBattle: function () {
                const myPlayer = game.getPlayer(this.selected);
                const another = Player.random();
                let result = myPlayer.randomBattleWith(another);
                this.resultText = result.textInfo;
            }
        }
    }
</script>

