import {Team} from "./team";

class Game {

    constructor() {
        this.store = '';
        this.season = 1;
        this.gameDay = 1;
        this.gold = 100;
        this.players = {};
        this.playerTeam = new Team();
    }

    get userPlayerIds() {
        return this.playerTeam.allPlayers;
    }

    get userPlayers() {
        const result = [];
        const ids = this.userPlayerIds;
        for (let id of ids) {
            result.push(this.players[id]);
        }
        return result;
    }

    get playerList() {
        const result = [];
        for (let index in this.players) {
            const player = this.players[index];
            result.push(player);
        }
        return result;
    }

    registerPlayer(player) {
        this.players[player.id] = player;
    }

    getPlayer(id) {
        return this.players[id];
    }
}

const game = new Game();

export {game}