<template>
    <div id="team">
        <el-card>首发球员</el-card>
        <div id="starterDiv">
            <player-card v-for="p in players" :id="p.id"></player-card>
        </div>
        <el-button @click="generate()">随机生成</el-button>
    </div>
</template>

<script>
    import {Player} from '../core/player'
    import {game} from '../core/game'
    import PlayerCard from "./PlayerCard";

    export default {
        name: "MyTeam",
        components: {PlayerCard},
        data() {
            return {
                players: game.userPlayers,
                styles: {
                    emptyCard: {
                        padding: '0px',
                        background: 'transparent',
                        height: '250px',
                        border: 'none'
                    }
                }
            }
        },
        created() {
            this.game = game;
        },
        methods: {
            generate: function () {
                this.$prompt('请输入球员名字', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                }).then(({value}) => {
                    const p = Player.randomAndReg();
                    p.name = value;
                    this.players.push(p);
                    this.game.playerTeam.addPlayer();
                }).catch(() => {
                });
            }
        }
    }
</script>

<style scoped>
    #starterDiv {
        display: flex;
        justify-content: space-between;
    }

    .el-card {
        margin-bottom: 10px;
    }
</style>