<template>
    <el-card :body-style="styles.card">
        <div class="inner-info">
            <el-row>
                <el-col :span="8"><span>头像</span></el-col>
                <el-col :span="16"><img :src="require('../assets/' + player.head + '.png')" class="image"/></el-col>
            </el-row>
            <el-row>
                <el-col :span="8"><span>姓名</span></el-col>
                <el-col :span="16"><span>{{ player.name || player.id }}</span></el-col>
            </el-row>
            <el-row>
                <el-col :span="8"><span>年龄</span></el-col>
                <el-col :span="16"><span>{{ player.age }}</span></el-col>
            </el-row>
            <el-row>
                <el-col :span="8"><span>潜力</span></el-col>
                <el-col :span="16">
                    <el-rate disabled v-model="player.potentialRate"></el-rate>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8"><span>能力</span></el-col>
                <el-col :span="16">
                    <el-rate disabled v-model="player.abilityRate"></el-rate>
                </el-col>
            </el-row>
            <div class="bottom clearfix">
                <el-tooltip class="item" effect="dark" placement="right">
                    <div slot="content" v-html="player.info"></div>
                    <el-button type="text">属性详情</el-button>
                </el-tooltip>
                <br/>
                <el-button type="text" class="button" @click="sell(p)">出售</el-button>
            </div>
        </div>
    </el-card>
</template>
<script>
    import {game} from "../core/game";

    export default {
        name: "PlayerCard",
        props: {
            id: ''
        },
        data() {
            return {
                player: {},
                styles: {
                    card: {
                        padding: '0px',
                        height: '250px',
                    },
                }
            }
        },
        created() {
            this.player = game.getPlayer(this.id);
            console.log(this.player);
        },
        methods: {
            sell: function (player) {
                this.game.gold += 20;
                this.players.splice(this.players.indexOf(player), 1);
            }
        }
    }
</script>

<style scoped>
    .el-card {
        margin-bottom: 20px;
        width: 200px;
    }

    .inner-info {
        text-align: left;
        padding: 10px;
    }

    .el-row + .el-row {
        margin-top: 5px;
    }

    .el-row > .el-col:first-of-type {
        color: #66b1ff;
    }
</style>